body{display:flex;margin:0 auto;color:var(--foreground-color);background:var(--background-color)}#root{width:100%;height:100%}main{display:grid;grid-template-columns:repeat(8, 1fr);gap:var(--grid-gap);width:100%;padding:calc(var(--header)*2) var(--margin);background:var(--background-color);overflow:hidden}@media screen and (max-width: 1023px){main{grid-template-columns:repeat(4, 1fr)}}section{grid-column:1/-1;color:var(--foreground-color);background:var(--background-color)}section.hero{padding-bottom:2rem;background-color:var(--background-color);box-shadow:2rem 0 0 var(--background-color),-2rem 0 0 var(--background-color),0 -10rem 0 2rem var(--background-color)}section.content{padding:2rem 0}section.footer{padding-top:2rem;background-color:var(--background-color);box-shadow:2rem 0 0 var(--background-color),-2rem 0 0 var(--background-color),0 10rem 0 2rem var(--background-color)}